<template>
    <b-container fluid class="content">
        <b-row class="text-center">
            <b-col>
            <b-card border-variant="light" header="API Key" class="text-center">
                <div>
                    <div class="generate-api-key">
                        <b-row>
                            <b-col cols="3" class="text-right">
                                <b-button size="sm" variant="outline-secondary" router-link to="/api-key/generate"> + Genarate API-Key </b-button>
                            </b-col>
                            <b-col cols="9">
                                <form @submit.prevent="search">
                                <b-form-input size="sm" v-model="searchText" placeholder="Search appname or username"></b-form-input>
                                </form>
                            </b-col>
                        </b-row>
                    </div>
                </div>
                <hr/>
                <b-card-text>
                      <div>
                        <alert-fail ref='alertFail'>
                            {{ errorMessage }}
                        </alert-fail>
                        <b-table class="api-key-table" :items="items" :fields="fields">
                            <template slot="status" slot-scope="row">
                                    <b-form-radio-group
                                        @change="statusChange(row.item.id)"
                                        class="key-status"
                                        :options="options"
                                        buttons
                                        button-variant="outline-secondary"
                                        size="sm"
                                        v-model="row.item.status"
                                    ></b-form-radio-group>
                            </template>
                        </b-table>
                    </div>
                </b-card-text>
            </b-card>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
import AlertFail from '@/components/AlertFail';
const stringUtil = require('../Utils/string-formater');

export default {
  data () {
    return {
      errorMessage: '',
      searchText: '',
      fields: ['appName', 'username', 'apiKey', 'status', 'generateDate'],
      items: [],
      options: [
        { text: 'on', value: 'on' },
        { text: 'off', value: 'off' }
      ]
    };
  },
  methods: {
    search () {
      this.getListApiKey();
    },
    statusChange (id) {
      this.$refs.alertFail.hide();
      const status = event.target.value;
      this.axios.post(process.env.VUE_APP_SERVICE_HOST + '/api-key/status/' + id, { status: status })
        .then(response => {
          if (response.data) {
            // change status finish
          }
        })
        .catch(error => {
          this.error = error.message;
          if (error.response) {
            this.errorMessage = error.response.data;
          }
          this.$refs.alertFail.show();
        });
    },
    setDefault () {
      this.items = [];
      this.$refs.alertFail.hide();
    },
    getListApiKey () {
      this.setDefault();
      this.axios.get(process.env.VUE_APP_SERVICE_HOST + '/api-key/search?search=' + this.searchText)
        .then(response => {
          if (response.data) {
            const items = response.data;
            for (let i = 0; i < items.length; i++) {
              let item = {
                id: items[i].id,
                appName: items[i].appName,
                username: items[i].username,
                apiKey: items[i].apiKey,
                status: items[i].status,
                generateDate: stringUtil.reformatDate(items[i].generateDate)
              };
              this.items.push(item);
            }
          }
        })
        .catch(error => {
          this.errorMessage = error.message;
          this.$refs.alertFail.show();
        });
    }
  },
  mounted () {
    this.getListApiKey();
  },
  components: {
    AlertFail
  }
};
</script>

<style>
    .generate-api-key{
        margin-bottom: 10px;
    }
    .api-key-table{
        font-size: 0.8em;
        color: #666666;
    }
    .key-status{
        margin-top: -5px;
        margin-bottom: -5px;
    }
</style>
