<template>
  <div>
    <b-alert
      variant="danger"
      dismissible
      @dismissed="showDismissibleAlert=false"
      :show="showDismissibleAlert"
      fade
    >
      <slot></slot>
    </b-alert>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showDismissibleAlert: false
    };
  },
  methods: {
    show () {
      this.showDismissibleAlert = true;
    },
    hide () {
      this.showDismissibleAlert = false;
    }
  }
};
</script>
